/* Import Bootstrap Blazor */
html,
body,
button,
.popover {
  font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
}

body,
.form-control,
.dropdown-menu,
.btn:not(.btn-xs):not(.btn-sm):not(.btn-md):not(.btn-lg):not(.btn-xl):not(.close),
.alert .close .fa-times,
.toast .close,
.input-group-text {
  font-size: 0.875rem;
}

:focus,
button:focus {
  outline: none;
}

a {
  cursor: pointer;
}

a,
a:hover,
a:focus {
  text-decoration: none;
  outline: none;
}

.form-control.is-invalid,
.was-validated .form-control:invalid,
.form-control.is-valid,
.was-validated .form-control:valid,
.form-control.valid.modified:not([type=checkbox]) {
  outline: none;
}

input:disabled,
.btn:disabled,
.datetime-picker-input-icon.disabled,
.datetime-picker-input.is-disabled,
.datetime-picker-input:disabled,
.upload.is-disabled {
  cursor: not-allowed;
}

@media (min-width: 576px) {
  .form-inline .control-label {
    padding-top: 6px;
    padding-bottom: 6px;
  }
}

.form-control:focus {
  border-color: #3395ff;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(51, 149, 255, 0.6);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(51, 149, 255, 0.6);
}

.form-inline .row {
  flex: 1;
}

.form-inline .form-group .control-label {
  margin-right: 10px;
  width: 90px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  display: block;
}

.was-validated .form-control:invalid:focus,
.form-control.is-invalid:focus,
.was-validated .custom-select:invalid:focus,
.custom-select.is-invalid:focus {
  border-color: #dc3545;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(220, 53, 69, 0.6);
}

.was-validated .form-control:valid:focus,
.form-control.is-valid:focus,
.was-validated .custom-select:valid:focus,
.custom-select.is-valid:focus {
  border-color: #28a745;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(40, 167, 69, 0.6);
}

.arrow-primary {
  border-color: transparent transparent #007bff;
}

.arrow-success {
  border-color: transparent transparent #28a745;
}

.arrow-info {
  border-color: transparent transparent #17a2b8;
}

.arrow-warning {
  border-color: transparent transparent #ffc107;
}

.arrow-danger {
  border-color: transparent transparent #dc3545;
}

.show .shadow-primary,
.shadow-primary:hover,
.shadow-primary:focus {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(0, 123, 255, 0.5);
  border-color: #007bff !important;
}

.show .shadow-success,
.shadow-success:hover,
.shadow-success:focus {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(40, 167, 69, 0.5);
  border-color: #28a745 !important;
}

.show .shadow-info,
.shadow-info:hover,
.shadow-info:focus {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(23, 162, 184, 0.5);
  border-color: #17a2b8 !important;
}

.show .shadow-warning,
.shadow-warning:hover,
.shadow-warning:focus {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(255, 193, 7, 0.5);
  border-color: #ffc107 !important;
}

.show .shadow-danger,
.shadow-danger:hover,
.shadow-danger:focus {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(220, 53, 69, 0.5);
  border: 1px solid #dc3545 !important;
}

.show .shadow-secondary,
.shadow-secondary:hover,
.shadow-secondary:focus {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px #f3f3f3;
  border: 1px solid #78828a !important;
}

.show .shadow-dark,
.shadow-dark:hover,
.shadow-dark:focus {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px #f3f3f3;
  border: 1px solid #343a40 !important;
}

.show .shadow-default,
.shadow-default:hover,
.shadow-default:focus {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(51, 149, 255, 0.6);
  border: 1px solid #0062cc;
}

/*pagination*/
.nav-pages {
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.pagination {
  margin: 0;
  flex: 1 1 auto;
  justify-content: flex-end;
}

.page-link:focus {
  box-shadow: none;
}

.pagination-items {
  display: inline-block;
}

.pagination-items .form-select {
  width: 100px;
  margin: 0 10px;
}

.pagination-items .form-select .dropdown-menu {
  min-width: unset;
  text-align: right;
}

.pagination-items .form-select .form-control {
  width: 100%;
}

.page-item:hover .page-link-prev .fa-ellipsis-h:before {
  content: "\f100";
}

.page-item:hover .page-link-next .fa-ellipsis-h:before {
  content: "\f101";
}

.page-link {
  padding: 0.5rem 0.35rem;
  line-height: 1;
}

.page-link .fa {
  width: 11px;
  text-align: center;
}

@media (min-width: 375px) {
  .page-link {
    padding: 0.5rem 0.5rem;
  }
}

@media (min-width: 456px) {
  .page-link {
    padding: 0.5rem 0.75rem;
  }
}

/*end pagination*/
/*swal*/
.swal .modal-header {
  display: none;
}

.swal .swal2-icon,
.swal .swal2-actions {
  display: flex;
}

.swal .swal2-actions {
  margin-bottom: 1rem;
}

/*end swal*/
/*timer*/
.timer .time-panel-footer {
  display: flex;
  justify-content: space-between;
}

.timer .time-panel-btn {
  border: 1px solid transparent;
  border-radius: 4px;
  transition: border 0.3s linear;
  padding: 0 0.65rem;
}

.timer .time-panel-btn:hover {
  border-color: #dee2e6;
}

.timer .time-panel-btn.confirm {
  color: #28a745;
}

.timer .circle-title {
  flex-flow: column nowrap;
  bottom: 66px;
}

.timer .timer-body {
  font-size: 3.5rem;
}

.timer .timer-alert {
  font-size: 1rem;
}

.timer .timer-alert i {
  margin-right: 0.5rem;
}

.timer .timer-buttons {
  display: flex;
  justify-content: space-between;
}

.timer .timer-buttons .btn {
  border: 5px double #868e96;
  border-radius: 50%;
  height: 66px;
  width: 66px;
  font-size: 0.75rem !important;
  padding: 0;
}

/*end timer*/
/*toggle*/
.toggle {
  position: relative;
  overflow: hidden;
  border: 1px solid #f3f3f3;
  min-width: 70px;
  min-height: 35px;
}

.toggle.disabled .toggle-group label,
.toggle.disabled .toggle-group span {
  cursor: not-allowed;
}

.toggle.off .toggle-group {
  left: -100%;
}

.toggle .btn-default,
.toggle.btn-default {
  background-color: #dee2e6;
  border-color: #adb5bd;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.15), 0 1px 1px rgba(0, 0, 0, 0.075);
}

.toggle .btn-default:hover {
  color: #343a40;
  background-color: #dee2e6;
}

.toggle .toggle-group {
  position: absolute;
  width: 200%;
  top: 0;
  bottom: 0;
  left: 0;
  -webkit-transition: left 0.35s;
  -moz-user-select: none;
  -webkit-user-select: none;
  transition: left 0.35s;
}

.toggle .toggle-group label {
  display: flex;
  align-items: center;
  justify-content: center;
}

.toggle .toggle-group label,
.toggle .toggle-group span {
  cursor: pointer;
}

.toggle .toggle-group .toggle-on.bg-dark {
  color: #dee2e6;
}

.toggle .btn-handle {
  position: relative;
  margin: 0 auto;
  padding-top: 0px;
  padding-bottom: 0px;
  height: 100%;
  width: 0px;
  border-width: 0 1px;
  background-image: linear-gradient(to bottom, #fff 0%, #dee2e6 100%);
  border-color: #adb5bd;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.15), 0 1px 1px rgba(0, 0, 0, 0.075);
}

.toggle .toggle-on {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 50%;
  margin: 0;
  border: 0;
  border-radius: 0;
}

.toggle .toggle-on.btn {
  padding-right: 24px;
}

.toggle .toggle-on.btn-lg {
  padding-right: 31px;
}

.toggle.off .toggle-off {
  color: #343a40;
}

.toggle .toggle-off {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  right: 0;
  margin: 0;
  border: 0;
  border-radius: 0;
  background-color: #dee2e6;
}

.toggle .toggle-off.btn {
  padding-left: 24px;
}

.toggle .toggle-off.btn-lg {
  padding-left: 31px;
}

.toggle.btn-lg {
  min-width: 79px;
  min-height: 45px;
}

.toggle-handle.btn-lg {
  width: 40px;
}

.toggle.btn-sm {
  min-width: 50px;
  min-height: 30px;
}

.toggle-on.btn-sm {
  padding-right: 20px;
}

.toggle-off.btn-sm {
  padding-left: 20px;
}

.toggle.btn-xs {
  min-width: 35px;
  min-height: 22px;
}

.toggle-on.btn-xs {
  padding-right: 12px;
}

.toggle-off.btn-xs {
  padding-left: 12px;
}

/*end toggle*/
/*progress*/
.form-inline .progress {
  width: 100%;
}

/*end progress*/
/*dropdown*/
.dropdown .dropdown-toggle:after,
.dropup .dropdown-toggle:after,
.dropright .dropdown-toggle:after,
.btn-group .dropdown-toggle:not(:empty):after {
  margin-left: 6px;
}

.dropleft .dropdown-toggle:before {
  margin-right: 6px;
}

.dropdown-menu {
  max-height: 186px;
  overflow: auto;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.dropdown-item {
  cursor: pointer;
}

/*end dropdown*/
.close {
  display: flex;
}

/*captcha*/
.captcha {
  border-radius: 4px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.125);
}

.captcha-refresh {
  float: right;
  cursor: pointer;
  padding-left: 0.5rem;
}

.captcha-body {
  position: relative;
}

.captcha-body .captcha-load {
  display: flex;
  align-items: center;
  justify-content: center;
}

.captcha-body .captcha-body-bg,
.captcha-body .captcha-body-bar {
  position: absolute;
  border-radius: 2px;
  top: 0;
  left: 0;
}

.captcha-footer {
  text-align: center;
  background: #f8f9fa;
  color: #343a40;
  border-radius: 2px;
  margin-top: 0.5rem;
  position: relative;
}

.captcha-footer,
.captcha-bar-bg {
  line-height: 40px;
  height: 40px;
}

.captcha-bar-bg {
  position: absolute;
  border-radius: 4px;
}

.captcha-bar {
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  background: #fff;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  border-radius: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.captcha-footer.is-move .captcha-bar-text {
  display: none;
}

.captcha-footer.is-move .captcha-bar-bg {
  border: 1px solid #1a88ff;
  background-color: #4da3ff;
}

.captcha-footer.is-valid .captcha-bar .fa-arrow-right:before {
  content: "\f00c";
}

.captcha-footer.is-invalid .captcha-bar {
  border: 1px solid #e04b59;
  background-color: #e04b59;
}

.captcha-footer.is-invalid .captcha-bar-bg {
  border: 1px solid #e04b59;
  background-color: #e77681;
  border-width: 1px 0 1px 1px;
}

.captcha-footer.is-invalid .captcha-bar .fa-arrow-right:before {
  content: "\f00d";
}

.captcha-footer.is-valid .captcha-bar {
  border: 1px solid #1ab6cf;
  background-color: #1ab6cf;
}

.captcha-footer.is-valid .captcha-bar-bg {
  border: 1px solid #1ab6cf;
  border-width: 1px 0 1px 1px;
  background-color: #36cee6;
}

.captcha-footer.is-valid .captcha-bar .fa-arrow-right:before {
  content: "\f00c";
}

/*end captcha*/
/*calendar*/
.calendar-header {
  display: flex;
  justify-content: space-between;
  padding: 12px 20px;
  border-bottom: 1px solid #f3f3f3;
}

.calendar-title {
  color: #000;
  align-self: center;
  font-size: 1rem;
}

.calendar-button-group .btn {
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 1px solid #dee2e6;
  color: #868e96;
  outline: none;
  margin: 0;
  transition: 0.3s;
  padding: 7px 15px;
  font-size: 12px;
}

.calendar-button-group .btn:focus,
.calendar-button-group .btn:hover {
  color: #4da3ff;
  border-color: #ced4da;
  background-color: #f3f3f3;
}

.calendar-button-group .btn:active {
  color: #1a88ff;
  border-color: #1a88ff;
  outline: none;
}

.calendar-table {
  table-layout: fixed;
  width: 100%;
}

.calendar-table thead th {
  padding: 12px 0;
  color: #868e96;
  font-weight: 400;
  text-align: center;
}

.calendar-table:not(.is-range) td.next,
.calendar-table:not(.is-range) td.prev {
  color: #f3f3f3;
}

.calendar-table tr td:first-child {
  border-left: 1px solid #f3f3f3;
}

.calendar-table tr:first-child td {
  border-top: 1px solid #f3f3f3;
}

.calendar-table td {
  border-bottom: 1px solid #f3f3f3;
  border-right: 1px solid #f3f3f3;
  vertical-align: top;
  transition: background-color 0.2s ease;
  cursor: pointer;
}

.calendar-table td.is-today {
  color: #4da3ff;
}

.calendar-table td.is-selected {
  color: #1a88ff;
}

.calendar-table td.is-selected {
  background-color: #f8f9fa;
}

.calendar-table .calendar-day {
  box-sizing: border-box;
  padding: 8px;
  height: 85px;
}

.calendar-table .calendar-day:hover {
  cursor: pointer;
  background-color: #f8f9fa;
}

.calendar-table:not(.is-range) td.next,
.calendar-table:not(.is-range) td.prev {
  color: #f3f3f3;
}

.calendar .table-week .week-header {
  border-radius: 50%;
  border: 1px solid transparent;
  padding: 4px;
  display: inline-block;
  min-width: 52px;
}

.calendar .table-week .week-header.is-today {
  border: 1px solid #4da3ff;
  color: #4da3ff;
}

.calendar .table-week thead tr:last-child {
  border-bottom: 2px solid #dee2e6;
}

.calendar .table-week tbody tr {
  text-align: center;
}

.calendar .table-week tbody td {
  padding: 1rem 0;
  border-right: 0;
  border-left: 0;
  position: relative;
}

/*end calendar*/
/*divider*/
.divider {
  background-color: #dee2e6;
  position: relative;
}

.divider .divider-text {
  position: absolute;
  background-color: #fff;
  padding: 0 20px;
  font-weight: 500;
  color: #939ba2;
  font-size: 14px;
}

.divider.divider-horizontal {
  display: block;
  height: 1px;
  width: 100%;
  margin: 1rem 0;
}

.divider.divider-horizontal .divider-text.is-left {
  left: 20px;
  transform: translateY(-50%);
}

.divider.divider-horizontal .divider-text.is-center {
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.divider.divider-horizontal .divider-text.is-right {
  right: 20px;
  transform: translateY(-50%);
}

.divider-vertical {
  width: 1px;
  margin: 0 1rem;
}

.divider.divider-vertical .divider-text {
  padding: 20px 0;
}

.divider.divider-vertical .divider-text.is-left {
  top: 20px;
  transform: translateX(-50%);
}

.divider.divider-vertical .divider-text.is-center {
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.divider.divider-vertical .divider-text.is-right {
  bottom: 20px;
  transform: translateX(-50%);
}

/*end divider*/
/*accordion*/
.accordion .card-header {
  padding: 6px;
}

.accordion .card-header-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 0.5rem;
}

.accordion .card-header-wrapper .fa-angle-down {
  transition: transform 0.3s linear;
}

.accordion .card-header-wrapper .collapsed + .fa-angle-down {
  transform: rotate(-90deg);
}

.accordion .collapse-item {
  font-size: 13px;
  line-height: 1.8;
}

/*end accordion*/
/*avatar*/
.avatar {
  border-radius: 4px;
  width: 50px;
  height: 50px;
  overflow: hidden;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: #f3f3f3;
  color: #fff;
}

.avatar img {
  width: 100%;
  object-fit: cover;
}

.avatar.is-circle {
  border-radius: 50%;
}

.avatar.is-xl {
  width: 70px;
  height: 70px;
}

.avatar.is-lg {
  width: 60px;
  height: 60px;
}

.avatar.is-sm {
  width: 40px;
  height: 40px;
}

.avatar.is-xs {
  width: 30px;
  height: 30px;
}

.avatar i {
  font-size: 1.4rem;
}

/*end avatar*/
/*tag*/
.tag {
  display: inline-block;
  height: 32px;
  padding: 0 10px;
  line-height: 30px;
  font-size: 12px;
  border-radius: 4px;
  white-space: nowrap;
  margin: 0;
}

.tag.is-close {
  padding-right: 30px;
}

.tag.is-close .close {
  top: 8px;
}

.tag > i {
  margin-right: 2px;
}

/*end tag*/
/*timeline*/
.timeline {
  margin: 0;
  padding: 0;
  list-style: none;
}

.timeline .time-line-item {
  position: relative;
  padding-bottom: 1rem;
}

.timeline .time-line-item:last-child {
  padding-bottom: 0;
}

.timeline .time-line-item .time-line-item-tail {
  position: absolute;
  left: 4px;
  height: 100%;
  border-left: 2px solid #dee2e6;
}

.timeline .time-line-item:last-child .time-line-item-tail {
  display: none;
}

.timeline .timeline-item-node-normal {
  left: -1px;
  width: 12px;
  height: 12px;
}

.timeline .timeline-item-node {
  position: absolute;
  background-color: #dee2e6;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.timeline .timeline-item-node.is-icon {
  background-color: transparent;
}

.timeline .timeline-item-wrapper {
  position: relative;
  padding-left: 1.5rem;
  top: -4px;
}

.timeline .timeline-item-content {
  color: #939ba2;
}

.timeline .timeline-item-timestamp {
  margin-top: 0.5rem;
  color: #868e96;
}

.timeline.is-alternate .time-line-item-tail,
.timeline.is-alternate .timeline-item-node {
  left: 50%;
}

.timeline.is-alternate .timeline-item-node {
  margin-left: -5px;
}

.timeline.is-alternate .time-line-item:nth-child(odd) .timeline-item-wrapper {
  left: calc(50% - 5px);
  width: calc(50% - 1rem);
  text-align: left;
}

.timeline.is-alternate .time-line-item:nth-child(even) .timeline-item-wrapper {
  width: calc(50% - 1rem);
  margin: 0;
  text-align: right;
}

.timeline.is-left .time-line-item-tail,
.timeline.is-left .timeline-item-node {
  left: 100%;
}

.timeline.is-left .timeline-item-node {
  margin-left: -5px;
}

.timeline.is-left .time-line-item .timeline-item-wrapper {
  width: calc(100% - 1rem);
  margin: 0;
  text-align: right;
}

/*end timeline*/
/*footer*/
.footer {
  background: #868e96;
  color: #fff;
  padding: 10px 4px;
  height: 40px;
  display: flex;
}

.footer .footer-text {
  flex: 1 1 auto;
  text-align: center;
}

/*end footer*/
/*step*/
.steps {
  display: flex;
}

.steps-horizontal {
  white-space: nowrap;
}

.step {
  position: relative;
  flex-shrink: 1;
}

.step.is-horizontal {
  display: inline-block;
}

.step.is-center .step-head,
.step.is-center .step-main {
  text-align: center;
}

.step-head {
  position: relative;
  width: 100%;
  line-height: 1;
}

.step-head.is-success {
  color: #48d368;
  border-color: #48d368;
}

.step-head.is-process {
  color: #939ba2;
  border-color: #939ba2;
}

.step-head.is-wait {
  color: #f3f3f3;
  border-color: #f3f3f3;
}

.step-head.is-finish {
  color: #4da3ff;
  border-color: #4da3ff;
}

.step-head.is-error {
  color: #e77681;
  border-color: #e77681;
}

.step-main {
  white-space: normal;
  text-align: left;
}

.step-title {
  font-size: 14px;
  line-height: 38px;
}

.step-title.is-success {
  color: #48d368;
}

.step-title.is-finish {
  color: #4da3ff;
}

.step-title.is-process {
  font-weight: 700;
