}

.step-description {
  padding-right: 10%;
  margin-top: -5px;
  font-size: 12px;
  line-height: 20px;
  font-weight: 400;
}

.step-description.is-success {
  color: #48d368;
}

.step-description.is-finish {
  color: #4da3ff;
}

.step-description.is-process {
  color: #939ba2;
}

.step-description.is-wait {
