}

.step.is-center .step-description {
  padding-left: 20%;
  padding-right: 20%;
}

.step-line {
  position: absolute;
  border-color: inherit;
  background-color: #f3f3f3;
}

.step.is-horizontal .step-line {
  height: 2px;
  top: 11px;
  left: 0;
  right: 0;
}

.step.is-center .step-line {
  left: 50%;
  right: -50%;
}

.step-line-inner {
  display: block;
  border: 1px solid;
  border-color: inherit;
  transition: 0.15s ease-out;
  box-sizing: border-box;
  width: 0;
  height: 0;
}

.step:last-of-type .step-line {
  display: none;
}

.step-icon {
  position: relative;
  z-index: 1;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  font-size: 14px;
  box-sizing: border-box;
  background: #fff;
  transition: 0.15s ease-out;
}

.step-icon.is-text {
  border-radius: 50%;
  border: 2px solid;
  border-color: inherit;
