.step-icon-inner {
  display: inline-block;
  user-select: none;
  text-align: center;
  font-weight: 700;
  line-height: 1;
  color: inherit;
  font-style: normal;
}

.step-icon-inner[class*="fa"]:not(.is-status) {
  font-size: 25px;
  font-weight: 400;
}

.step:last-of-type.is-flex {
  flex-basis: auto !important;
  flex-shrink: 0;
  flex-grow: 0;
}

/*end step*/
