/*listview*/
.listview {
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
}

.listview .listview-header {
  padding: 1rem;
