  border-bottom-width: 1px;
  border-bottom-style: solid;
}

.listview .listview-body {
  padding: 1rem 0 0 1rem;
  position: relative;
  overflow: auto;
  display: flex;
  flex-flow: row wrap;
  flex: 1;
}

.listview .listview-body .listview-item {
  margin: 0 1rem 1rem 0;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

